.card {
  box-sizing: border-box;

  &__wrapper {
    overflow: hidden;
    width: fit-content;
  }

  &__list {
    display: flex;
    flex-direction: row;
    gap: 16px;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__body {
    display: flex;
    flex-direction: column;

    width: 272px;
    height: 504px; // принудительная высота

    padding: 0;
    margin: 0;
  }

  &__img{
    width: 208px;
    height: 208px;

    // margin: 32px 32px 24px;
  }

  &__title {
    display: flex;
    padding: 0;
    margin: 0;

    font-family: 'Mont';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #313237;
  }

  &__description {
    padding: 0;
    margin: 0;
    font-family: 'Mont';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 140%;
    color: #313237;

    &-line {
      text-decoration: line-through;
      margin-left: 8px;
      font-family: 'Mont';
      font-style: normal;
      font-weight: 500;
      font-size: 22px;
      line-height: 28px;
      color: #89939a;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 16px;
    // align-items: flex-end;
    &-parameters {
      display: flex;
      justify-content: space-between;
      padding: 0;
      margin: 0;
      color: #89939a;
      font-family: 'Mont';
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
    }

    &-parameter {
      font-family: 'Mont';
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      color: #313237;
    }
  }

  &__btn {
    padding: 0;
    background-color: #313237;
    font-family: 'Mont';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    color: #fff;

    border: none;
    width: 176px;
    height: 40px;
  }

  &__add-section {
    display: flex;
    flex-direction: row;
    gap: 8px;
  }

  &__stroke {
    box-sizing: border-box;
    background-repeat: no-repeat;
    border: 1px solid #e2e6e9;
    background-position: center;
    width: 40px;
    height: 40px;
    background-image: url(../../src/images/Stroke.svg);
  }
}

.img-container {
  display: flex;
  justify-content: center;
  padding: 8px 8px 24px;
}

.phone-container {
  box-sizing: border-box;
  border: 1px solid #e2e6e9;
  padding: 24px;

  &:hover {
    transition: all 500ms;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
}

.line {
  border-top: 1px solid #e2e6e9;
  margin: 8px 0 16px;
}

.hotPrice {
  margin: 0;
  font-family: 'Mont';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: -0.01em;
  color: #313237;
}

.carousel-buttonSection {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 28px;
  // gap: 16px;
}

.buttons {
  content: '';
  background-color: #fff;
  border: 1px solid #e2e6e9;
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &:active {
    border: 1px solid #b4bdc3;

  }
}

.left {
  background-image: url(../images/vector-left.svg);
}

.right {
  background-image: url(../images/vector-right.svg);
}

.button-wraper {
  display: flex;
  gap: 16px;
}
