.category-container {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.category {
  text-decoration: none;
}

.title-category {
  font-family: 'Mont';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: -0.01em;
  color: #313237;
  margin: 0;

  &__phones {
    font-family: 'Mont';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #313237;
    margin: 0;

    &-mod {
      font-family: 'Mont';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: #89939A;
      margin: 0;
    }
  }
}

.box {
  width: 368px;
  height: 368px;
  background-repeat: no-repeat;
  background-position: right bottom;
}

.phones {
  background-color: #fcdbc1;
  background-image: url(../images/category-phones.png);
}

.tablets {
  background-color: #8d8d92;
  background-image: url(../images/category-tablets.png);
}

.accessories {
  background-color: #973d5f;
  background-image: url(../images/category-accessories.png);
}
