.cards__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}

.searchSection {
  display: flex;
  flex-direction: row;
  gap: 16px;

  &__selector {
    border: 1px solid #bcc4ca;
    background-color: #fff;
    
    font-family: 'Mont';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    color: #313237;
    padding: 10px 20px 10px 12px;
    min-width: 128px;
  }
}

