.main {
  // padding: 30px 152px 80px;
  display: flex;
  justify-content: center;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #e2e6e9;
  padding-left: 24px;

  &__link {
    text-decoration: none;
    font-family: Mont;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 11px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #89939a;

    @media screen and (max-width: 940px) {
      display: none;
    }

  }

  &__icons {
    display: flex;

    &-stroke {
      background-image: url(../../src/images/Stroke.svg);
    }

    &-shoppingBag {
      background-image: url(../../src/images/ShoppingBag.svg);
    }
  }
}

.hamburger {
  display: none;
  content: '';
  width: 16px;
  height: 18px;
  background-color: #313237;
  background-image: url(../images/menu.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media screen and (max-width: 940px) {
    display: block;
  }
}

.active-link {
  position: relative;

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: #313237;
    position: absolute;
    left: 0;
    bottom: -27px;
  }

  color: red;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e2e6e9;
  padding: 0 152px 0;

  @media screen and (max-width: 940px) {
    padding: 0 24px 0;
    justify-content: space-between;
  }

  &__link {
    text-decoration: none;
    font-family: Mont;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 11px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #89939a;

    &-top {
      display: inline;
      cursor: pointer;
      font-family: 'Mont';
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      text-align: right;
      color: #89939a;
      text-decoration: none;
    }
  }

  &__button {
    display: inline-block;
    cursor: pointer;
    border: 1px solid #e2e6e9;
    background-image: url(../../src/images/topArrow.svg);
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: center;
    margin-top: 16px;
    margin-bottom: 16px;
  }

  &__top {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;
  }
}

.footer-center {
  display: flex;
  gap: 64px;
  margin: 0 auto;

  @media screen and (max-width: 940px) {
    display: none;
  }
}

.container {
  display: flex;
  align-items: center;
  gap: 64px;

  // @media screen and (max-width: 940px) {
  //   position: absolute;
  //   flex-direction: column;
  //   background-color: rgba(0, 0, 0, .8);
  // }
}

.logo {
  width: 40px;
  background-image: url(../../src/images/LOGO.png);
  width: 40px;
  height: 11px;
  background-repeat: no-repeat;
  background-position: center;
  // padding-left: 24px;
}

.interactive {
  background-repeat: no-repeat;
  border-left: 1px solid #e2e6e9;
  padding: 24px;
  background-position: center;
  width: 16px;
  height: 16px;
}
